body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/*Font Digital for display and equation*/
@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg");
}

/*Calculator view display*/
#view{
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: inherit;
  text-align: end;
  font-family: digital;
}

/*Display result view*/
#dis {
  font-size: 3vw;
  padding-right: 5%;
  min-height: 80px;
}

/*Equation videw*/
#eq {
  padding-right: 5%; 
  font-size: 1.5vw;
  min-height: 30px;
}

/*Button equals*/
#equals{
  position: absolute;
  height: 12em;
  padding: 1.1em;
  z-index: 1;
}

@media (max-width:450px){
  /*Display result view responsive*/
  #dis {
    font-size: 14vw;
  }

  /*Equation videw responsive*/
  #eq {
    font-size: 5.5vw;
  }

  #clear{
    padding: .5rem 0px;
  }
}
